import React, { Component } from "react";
import "./reponse.css";

class Question extends Component {
  state = {};

  render() {
    return (
      <div className="container_reponse">
        <p className="reponse">{this.props.reponseValue}</p>
        <p className="description">{this.props.description}</p>
        <button className="buttonNext" onClick={() => this.props.nextQuestion()}>
          Prochaine question
        </button>
      </div>
    );
  }
}

export default Question;
