import React, { Component } from "react";
import "./finalPage.css";

class FinalPage extends Component {
  state = {
    buttonClicked: false,
    buttonClicked2: false,
  };

  triggerAnim = (x) => {
    if (x === 0) {
      this.setState((prevState) => ({
        buttonClicked: !prevState.buttonClicked,
      }));
    } else {
      this.setState((prevState) => ({
        buttonClicked2: !prevState.buttonClicked2,
      }));
    }
  };

  render() {
    const { buttonClicked, buttonClicked2 } = this.state;
    return (
      <div className="container">
        <div className="header">
          <p className="header_titlee">Quizz terminé !</p>
          <span className="span"></span>
          <p className="header_miniscore">
            Vous avez obtenu un score de {this.props.score}/{this.props.questionLength}
          </p>
          <p className="header_subtitle">Provenance des déchêts</p>
        </div>
        <div className="footer">
          <div className="allContainer">
            <div className="allImg_container">
              <div className={`text_img_container img_a ${buttonClicked ? "animOn" : " animOff"}`}>
                <div className="img_container ">
                  <img src={process.env.PUBLIC_URL + "/img/collectivites.png"} alt="" />
                </div>
                <p className="text">collectivités</p>
                <p className="text">4M</p>
              </div>

              <div className={`text_img_container img_b ${buttonClicked ? "animOn" : " animOff"}`}>
                <div className="img_container ">
                  <img src={process.env.PUBLIC_URL + "/img/menager.png"} alt="" />
                </div>
                <p className="text">déchets ménagers</p>
                <p className="text">31M</p>
              </div>

              <div className="text_img_container img_c">
                <div className="img_container" onClick={() => this.triggerAnim(0)}>
                  <img src={process.env.PUBLIC_URL + "/img/garbage.png"} alt="" />
                </div>
                <p className="text">provenance des déchets</p>
              </div>

              <div className={`text_img_container img_d ${buttonClicked ? "animOn" : " animOff"}`}>
                <div className="img_container padding3">
                  <img src={process.env.PUBLIC_URL + "/img/construction.png"} alt="" />
                </div>
                <p className="text">construction</p>
                <p className="text">247M</p>
              </div>

              <div className={`text_img_container img_e ${buttonClicked ? "animOn" : " animOff"}`}>
                <div className="img_container">
                  <img src={process.env.PUBLIC_URL + "/img/economique.png"} alt="" />
                </div>
                <p className="text">activités économiques</p>
                <p className="text">63M</p>
              </div>
            </div>

            <div className="nb_containerAll">
              <div className="nb_container nb_container1">
                <div className="img_container">
                  <img src={process.env.PUBLIC_URL + "/img/money.png"} alt="" />
                </div>
                <p className="text">16,7 Millions d’€</p>
              </div>
              <div className="nb_container nb_container2">
                <div className="img_container">
                  <img src={process.env.PUBLIC_URL + "/img/pelle.png"} alt="" />
                </div>
                <p className="text">126 Millions</p>
              </div>
              <div className="nb_container nb_container3">
                <div className="img_container">
                  <img src={process.env.PUBLIC_URL + "/img/kg.png"} alt="" />
                </div>
                <p className="text">7 à 10 Milliards</p>
              </div>
            </div>

            <div className="allImg_container2">
              <div
                className={`pourcentage_pays_container img_a2 ${
                  buttonClicked2 ? "animOn2" : " animOff2"
                }`}
              >
                <div className="pourcentage_container ">
                  <p className="pourcentage">28,1%</p>
                </div>
                <p className="text">Chine</p>
              </div>

              <div
                className={`pourcentage_pays_container img_b2 ${
                  buttonClicked2 ? "animOn2" : " animOff2"
                }`}
              >
                <div className="pourcentage_container ">
                  <p className="pourcentage">15,2%</p>
                </div>
                <p className="text">États-Unis</p>
              </div>

              <div
                className={`pourcentage_pays_container img_c2 ${
                  buttonClicked2 ? "animOn2" : " animOff2"
                }`}
              >
                <div className="pourcentage_container ">
                  <p className="pourcentage">3,4%</p>
                </div>
                <p className="text">Japon</p>
              </div>

              <div className="pourcentage_pays_container img_d2">
                <div className="img_container" onClick={() => this.triggerAnim(2)}>
                  <img src={process.env.PUBLIC_URL + "/img/co2.png"} alt="" />
                </div>
                <p className="text">Émission de CO2 par pays</p>
              </div>

              <div
                className={`pourcentage_pays_container img_e2 ${
                  buttonClicked2 ? "animOn2" : " animOff2"
                }`}
              >
                <div className="pourcentage_container ">
                  <p className="pourcentage">10,3%</p>
                </div>
                <p className="text">Europe</p>
              </div>

              <div
                className={`pourcentage_pays_container img_f2 ${
                  buttonClicked2 ? "animOn2" : " animOff2"
                }`}
              >
                <div className="pourcentage_container ">
                  <p className="pourcentage">7,3%</p>
                </div>
                <p className="text">Inde</p>
              </div>

              <div
                className={`pourcentage_pays_container img_g2 ${
                  buttonClicked2 ? "animOn2" : " animOff2"
                }`}
              >
                <div className="pourcentage_container ">
                  <p className="pourcentage">4,6%</p>
                </div>
                <p className="text">Russie</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default FinalPage;
