import React, { Component } from "react";
import "./question.css";

class Question extends Component {
  state = {};

  verifyAnswer = (value) => {
    let correct = false;
    if (value === this.props.reponse) correct = true;
    this.props.verifyQuestion(correct);
    this.props.printDescription();
  };

  render() {
    return (
      <div className="questionPropositionContainer">
        <button className="button" onClick={() => this.verifyAnswer("+")}>
          +
        </button>
        <p className="proposition">{this.props.value}</p>
        <button className="button" onClick={() => this.verifyAnswer("-")}>
          -
        </button>
      </div>
    );
  }
}

export default Question;
